@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#pagereturn{
    height: 100%;
    width: 100%;
    color: #ffffff;
}
#aboutgrid{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    color: #ccd6f6;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}

#aboutsection{
    grid-area: 2/3/8/9;
    display:flex;
    flex-direction: column;
}

#aboutwrappertop{
    padding-top: 2rem;
    font-size: 1.365vw;
    line-height: 0;
    flex: 0 1;
}

#aboutwrappertop > h3{
    margin-bottom: 2rem !important;
}

#aboutwrapperbottom{
    color: #a8b2d1;
    font-size: .98vw;
    flex: 1 1 70%;
    display: flex;
    flex-direction: column;
}

#abouttext > p{
    margin: 1rem;
    margin-left: 0rem;
}

#skills{
    display: flex;
    flex-direction: column;
}

#skillhighlight{
    margin-top: 2rem;
    padding-right: 9rem;
    color: #ccd6f6;
    font-weight: bold;
    font-size: 1.365vw;
    flex: 1 1 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#skillslist{
    padding-right: 9rem;
    font-size: .98vw;
    flex: 1 1 90%;
    display: flex;
    justify-content: space-around;
}

#skillslist > ul{
    list-style-type: none;
}
#experiencemain{
    height: 100%;
    width: 100%;
    background-color: blue;
}

#experiencegrid{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    color: #ccd6f6;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}

#experienceheader{
    font-size: 1.55vw;
    margin-bottom: 0rem;
}

.experiencecompany{
    font-weight: bold;
    font-size: .98vw;
}

.experiencedate{
    font-size: .7vw;
    line-height: 1.5rem;
}

.experiencebody{
    color: #a8b2d1;
    margin-top: -.5em;
    line-height: 0;
    font-size: .75vw;
}

.experiencebody > ul{
    list-style: none;
    line-height: 1.5rem;
}

.experiencebody ul li::before{
    content: "\2022";
    margin-left: -1em;
    margin-right: 1em;
    width: 2em;
    color:#64ffda;
}

#experiencesection{
    grid-area: 2/3/9/11;
    display:flex;
    flex-direction: column;
}

.companyname{
    color: #64ffda;
}

#experiencewrappertop{
    flex: 0 1 5%;
}

#experienceheader{
    line-height: 0;
}

#experiencewrapperbottom{
    flex: 1 1 95%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#projectsmain{
    height: 100%;
    width: 100%;
    background-color: blue;
}

#projectsgrid{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    color: #ccd6f6;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}

#projectsheader{
    font-size: 1.55vw;
    margin-bottom: 0rem;
}

.projectstitle{
    font-weight: bold;
    font-size: .98vw;
}

.projectsdate{
    font-size: .7vw;
    line-height: 1.5rem;
}

.projectsbody{
    color: #a8b2d1;
    margin-top: -.5em;
    line-height: 0;
    font-size: .7vw;
}

.projectsbody > ul{
    list-style: none;
    line-height: 1.5rem;
}

.projectsbody ul li::before{
    content: "\2022";
    margin-left: -1em;
    margin-right: 1em;
    width: 2em;
    color:#64ffda;
}

#projectssection{
    grid-area: 1/3/9/11;
    display:flex;
    flex-direction: column;
}

.titlename{
    color: #64ffda;
}

#projectswrappertop{
    margin-top: 3em;
    flex: 0 1 5%;
}

#projectsheader{
    line-height: 0;
}

#projectswrapperbottom{
    flex: 1 1 95%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
#contactgrid{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    color: #ccd6f6;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}

#contactsection{
    margin-top: -1em;
    grid-area: 2/3/10/9;
    height: 100%;
    width: 100%;
    display:flex;
    flex-direction: column;
}

#contactwrappertop{
    flex: 1 1 15%;
    padding-top: 2rem;
    font-size: 35px;
    line-height: 0;
    flex: 0 1;
}

#contactwrappertop > h4{
    margin-bottom: 3rem !important;
}

#contactwrapperbottom{
    font-size: 25px;
    flex: 1 1 85%;
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem !important;
}

#contactrow1{
    flex: 1 1 2%;
    display: flex;
    flex-direction: column;
}

.contactsubrow{
    flex: 1 1 50%;
}

.contactsubrow > h4{
    margin-top: 0px;
    margin-bottom: 0px;
}

#contactrow2{
    margin-top: 1rem;
    flex: 1 1 2%;
    display: flex;
    flex-direction: column;
}

#contactrow3{
    flex: 1 1 80%;
    display: flex;
    flex-direction: column;
}

#textbox{
    display: inline-block;
    flex: 1 1 100%;
    height:100%;
    display: flex;
    border:3px solid white;
}

#email, #name, #subject{
    box-sizing: border-box;
    font-size:24px;
    display:block;
    font-weight: 600;
    width:100%;
    height:100%;
    padding:5px 10px;
    background:#303C55;
    color: #ccd6f6;
    border:3px solid #172a45;
    border-radius: 7px;
    transition:border-color .25s ease;
}

#emailsubrow, #namesubrow, #subject{
    display: flex;
    flex: 1 1 90%;
}

#h3name, #h3email, #h3message{
    color: #ccd6f6;
    display: flex;
    flex-direction: column;
}

#textsubrow{
    display: flex;
    flex: 1 1 90%;
}

#h3message{
    flex: 1 1 2%;
}

#h3message > h4{
    margin-top: 1rem;
    margin-bottom: 0rem !important;
}

#subject{
    flex: 1 1 95%;
}
#splashgrid{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    color: #ccd6f6;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}

#herosection{
    grid-area: 3/3/9/11;
    display:flex;
    flex-direction: column;
    font-size: 1.365vw;
}

#herowrappertop{
    padding-top: 2rem;
    flex: 0 1;
}

#heroheader{
    line-height: 0;
}

#heroheader > h1{
    margin-bottom: 2rem;
}

#herowrapperbottom{
    flex: 1 1 70%;
    display: flex;
}

#herotext{
    color: #a8b2d1;
    padding-left: 4px;
    flex: 1 1 45%;
}

#heroimage{
    padding-right: 3rem;
    flex: 1 1 55%;
}

#heroimage >img {
    width:50%;
    height:auto;
}

#mainarea {
    background-color: #0a192f;
    position: absolute;
    height: 100%;
    width: 100%;
    display:grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
  }

#backgroundcontainer{
    grid-area: 1/3/11/9;
    display:flex;
    flex-direction: column;
}

#leftcontainer{
    grid-area: 1/1/11/3;
    display:grid;
    grid-template-rows: repeat(20, 5%);
    grid-template-columns: repeat(20, 5%);
}

#contactcontainer{
    box-sizing: border-box;
    grid-area: 16/17/21/21;
    display: flex;
    flex-direction: column;
}

#githubcontainer, #linkedincontainer{
    flex: 1 1 50%;
    display: flex;
    align-items: flex-end;
    text-align: right;
}

#githubcontainer{
    flex: 1 1;
}

#githubcontainer img{
    max-width: 70%;
    height: auto;
    padding-bottom: 1rem;
}

#linkedincontainer img{
    max-width: 69%;
    height: auto;
}

#navbar {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: .78vw;
    color: #64ffda;
    flex: 1 1 5%;
    /* background-color: #0a192f; */
    max-width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

#maincontainer{
    flex: 1 1 95%;
    flex-grow: 1;
}

.navitem, #nameholder{
    border-radius: 7px;
    box-sizing: border-box;
    flex: 0 1 12%;
    height: 100%;
    width: 100%;
    display:grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}


#nameholder{
    flex: 0 1 17%;
    margin-right: auto;
}

#nameholder > div{
    font-size: .86vw;
    font-weight: 500;
    text-align: center;
    grid-area: 4/1/11/11;
}

.navitem > div{
    text-align: center;
    grid-area: 4/1/11/11;
}

.navitem:hover, #nameholder:hover{
    background-color: #172a45;
    transition: all .2s ease-in;
}


